@import '../../UI/styles/styles.scss';

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 20px;
    padding: 10px;
    margin: 15px;
    border-left: 3px solid $blue;
    border-right: 3px solid $blue;
    font-weight: bold;
    font-size: 16px;
    .headName {
        text-transform: uppercase;
        color: black;
        font-size: 23px;
    };
    .basketHeader {
        display: flex;
        align-items: center;
        color: black;
        img {
            width: 30px;
        }
        div {
            margin-left: 10px;
        }
    }
}

@media (max-width: 420px) {
    .header {
        border: none;
        border-bottom: 2px solid #ccc;
        font-size: 12px;
        padding: 0;
        margin: 0;
        .basketHeader {
            display: flex;
            flex-direction: column;
        }
        .headName {
            font-size: 16px;
        }
    }
}