@import "../../UI/styles/styles.scss";

.productsPagination {
    display: flex;
    margin-left: 30px;
    .btnPagination {
        @include pagination;
    }
    .active {
        background-color: $red;
        border-color: $red;
        color: $white;
        box-shadow: $boxShadowHover;
        transform: scale(1.2);
        transition: 0.1s;
    }
}
.productButton {
    @include button;
}

.productsBox {
    padding: 30px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 4em;
    width: 600px;
    margin: 0 auto;
    text-align: center;
    .bookCard {
        position: relative;
        width: 200px;
        box-shadow: $boxShadowHover;
        display: flex;
        flex-direction: column;
        text-align: center;
        margin-top: 50px;
        padding: 30px;
        transition: 0.3s;
        font-size: 14px;
        &:hover {
            transform: scale(1.1);
            transition: 0.3s;
        }
        img {
            filter: $dropShadowIng;
            width: 170px;
            margin: 0 auto;
        }
        &:nth-child(1) {
            margin-top: 10px;
        }
        &:nth-child(2) {
            margin-top: 10px;
        }
    }
    .productPrice {
        font-size: 16px;
        font-weight: bold;
        margin-top: 10px;
        margin-bottom: 10px;
    }
}

@media (max-width: 1024px) {
    .productsBox {
        grid-gap: 0;
        .bookCard {
            width: 150px;
            text-align: center;
            font-size: 12px;
            img {
                width: 100px;
            }
        }
    }
}
@media (max-width: 768px) {
    .productsBox {
        width: 300px;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        align-items: center;
        .bookCard {
            margin: 30px;
        }
    }

    .btnPagination {
        max-width: 20px;
    }
}
@media (max-width: 420px) {
    .productsBox {
        .bookCard {
            width: 200px;
        }
    }
}
